.main {
  display: flex;
  flex-Direction: column;
  justify-Content: top;
  width: 1280px;
  height:720px;
}

.toolbar {
  display: flex;
  flex-Direction: row;
  justify-Content: center;
  background: #424D56;
  flex: 0 0 32px;
}

.workSpace {
  display: inline-flex;
  flex-Direction: row;
  justify-Content: center;
  flex: 1 0;
}

.infomationSpace {
  display: flex;
  flex: 1 0;
  background: #545D6C;
}


.mainEditSpace {
  display: flex;
  flex-Direction: column;
  justify-Content: top;
  flex: 5.4 0;
}